@import "components/header";
@import "components/asside";
@import "components/footer";

body{
  .content{
    margin: 25px 0 35px;
    .container{
      .row{
        .col-lg-12{
          p{
            padding: 0 60px 0 0;
            font: 3.438em/105px "BravoSC";
            color: #d10000;
            text-align: right;
            text-transform: uppercase;
            background-image: url("../img/vh_logo_large.jpg");
            background-size: cover;
          }
        }
      }
      .row{
        .col-lg-3{
          .aside{
            &__vhLogo{
              display: none;
            }
          }
        }
        .col-lg-9{
          .customize{
            margin: 30px 0;
            &__headline{
              margin: 0 0 65px 0;
              p{
                font: .875em/1.25em "Arial";
                color: #525252;
              }
            }
            &__sideSelect{
              margin: 0 0 25px 0;
              display: flex;
              align-items: center;
              span{
                display: inline-block;
                width: 115px;
                height: 40px;
                font: 1.125em/40px "Arial";
                font-weight: bold;
                text-align: center;
                color: #aeaeae;
                text-transform: uppercase;
                background-color: #eaeaea;
                border: 1px solid #999;
                &:hover{
                  cursor: pointer;
                }
              }
              #side1{
                margin: 0 20px 0 0;
              }
              #side2{
                margin: 0 0 0 20px;
              }
              .active{
                color: #555;
                border-color: #33c7ff;
              }
            }
            &__firstStep{
              ul{
                display: flex;
                justify-content: space-between;
                li{
                  width: 45%;
                  a{
                    padding: 25px;
                    display: block;
                    font: 1.25em/1.5em "Arial";
                    font-weight: bold;
                    color: #000;
                    text-decoration: none;
                    background-color: #eaeaea;
                    border: 1px solid #bbb;
                  }
                  &:nth-child(1){
                    a{
                      border-color: #33c7ff;
                    }
                  }
                }
              }
              .itemShape{
                margin: 30px 0;
                display: flex;
                flex-wrap: wrap;
                border: 1px solid #d0d0d0;
                .item{
                  padding: 30px;
                  width: 50%;
                  display: flex;
                  flex-direction: column;
                  align-items: center;
                  .shape{
                    img{
                      width: 100%;
                      &:hover{
                        cursor: pointer;
                      }
                    }
                  }
                  .shapeDescr{
                    margin: 20px 0 0 25px;
                    p{
                      font: 1.063em/1.438em "Arial";
                      font-weight: bold;
                      white-space: nowrap;
                    }
                    span{
                      font: 1.063em/1.313em "Arial";
                      white-space: nowrap;
                    }
                  }
                }
              }
              .forward{
                display: flex;
                justify-content: center;
                a{
                  margin: 30px 0 40px;
                  width: 215px;
                  display: block;
                  font: 1.063em/40px "Arial";
                  color: #fff;
                  text-align: center;
                  text-transform: uppercase;
                  text-decoration: none;
                  background-color: #009fff;
                }
              }
            }
            &__secondStep{
              display: flex;
              align-items: center;
              background-color: #ededed;
              border: 1px solid #d0d0d0;
              .hologram{
                padding: 10px;
                width: 50%;
                ul{
                  display: flex;
                  flex-direction: column;
                  justify-content: center;
                  align-items: center;
                  li{
                    width: 100%;
                    display: flex;
                    justify-content: center;
                    span{
                      width: 14px;
                      height: 75px;
                      display: block;
                    }
                    img{
                      margin: 25px;
                      width: 100%;
                      box-shadow: 5px 5px 15px #777;
                    }
                    &:nth-child(1){
                      span{
                        margin: 0 0 45px;
                        background: url("../img/arrow_up.png") no-repeat;
                        background-size: cover;
                      }
                    }
                    &:nth-child(3){
                      span{
                        margin: 45px 0 0;
                        background: url("../img/arrow_down.png") no-repeat;
                        background-size: cover;
                      }
                    }
                  }
                }
              }
              .hologramOptions{
                width: 50%;
                .layer-1,
                .layer-2,
                .layer-3{
                  h6{
                    margin: 15px 0;
                    padding: 0 0 0 15px;
                    font: 1.25em/35px "Arial";
                    font-weight: bold;
                    background-color: #d9d9d9;
                  }
                  .companyName,
                  .companySecurity,
                  .companyOriginal,
                  .companyTop{
                    padding: 7.5px 0;
                    display: flex;
                    flex-wrap: wrap;
                    align-items: center;
                    div{
                      margin: 7px 0;
                    }
                    .name{
                      input{
                        margin: 0 5px 0 0;
                        padding: 0 0 0 20px;
                        width: 220px;
                        font: 1.063em/40px "Arial";
                        color: #000;
                        border: 1px solid #d0d0d0;
                      }
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
                      height: 40px;
                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                    .color{
                      width: 150px;
                      input{
                        margin: 0 5px 0 0;
                        width: 120px;
                        font: 1.063em/40px "Arial";
                        color: #000;
                        text-align: center;
                        border: 1px solid #d0d0d0;
                      }
                    }
                    .fontSize{
                      width: 175px;
                      display: flex;
                      align-items: center;
                      p{
                        font: 1.063em/40px "Arial";
                      }
                      input{
                        margin: 0 5px 0 8px;
                        width: 40px;
                        height: 40px;
                        font: 1.063em/40px "Arial";
                        text-align: center;
                        text-transform: uppercase;
                        border: 1px solid #d0d0d0;
                      }
                      span{
                        display: flex;
                        flex-direction: column;
                        i{
                          color: #427b94;
                          &:hover{
                            cursor: pointer;
                          }
                        }
                      }
                    }
                    .fontWeight{
                      display: flex;
                      align-items: center;
                      p{
                        margin: 0 8px 0 0;
                        font: 1.063em/40px "Arial";
                      }
                      input[type="checkbox"]{
                        display: none;
                      }
                      input[type="checkbox"] + span:before{
                        font-family: "FontAwesome";
                      }
                      input[type="checkbox"]:checked + span:before{
                        content: "\f111";
                        font-size: 10px;
                        line-height: 20px;
                        color: #6d6d6d;
                      }
                      label{
                        width: 20px;
                        height: 20px;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        background-color: #fff;
                        border: 1px solid #d0d0d0;
                        border-radius: 50px;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                  .companyName{
                    .color{
                      input{
                        color: #000;
                        background-color: #07ff00;
                      }
                      input::-webkit-input-placeholder{
                        color: #000;
                      }
                      input:-moz-placeholder{
                        color: #000;
                      }
                    }
                  }
                  .companySecurity{
                    .color{
                      input{
                        color: #fff;
                        background-color: #ff0000;
                      }
                      input::-webkit-input-placeholder{
                        color: #fff;
                      }
                      input:-moz-placeholder{
                        color: #fff;
                      }
                    }
                  }
                  .companyOriginal{
                    .color{
                      input{
                        color: #fff;
                        background-color: #002aff;
                      }
                      input::-webkit-input-placeholder{
                        color: #fff;
                      }
                      input:-moz-placeholder{
                        color: #fff;
                      }
                    }
                  }
                  .companyTop{
                    .color{
                      input{
                        color: #000;
                        background-color: #fff;
                      }
                      input::-webkit-input-placeholder{
                        color: #000;
                      }
                      input:-moz-placeholder{
                        color: #000;
                      }
                    }
                  }
                  .selectFile{
                    padding: 7.5px 0 15px;
                    display: flex;
                    input{
                      width: 0.01px;
                      height: 0.01px;
                      opacity: 0;
                      overflow: hidden;
                      z-index: -1;
                    }
                    input + label{
                      margin: 0 65px 0 0;
                      min-width: 160px;
                      display: block;
                      font: 1.063em/40px "Arial";
                      color: #fff;
                      text-align: center;
                      text-decoration: none;
                      background-color: #427b94;
                    }
                    input:focus + label,
                    input + label:hover{
                      background-color: #009fff;
                    }
                    input + label{
                      cursor: pointer;
                    }
                    input:focus + label{
                      outline: 1px solid #fff;
                      outline: -webkit-focus-ring-color auto 5px;
                    }
                    input + label * {
                      pointer-events: none;
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
                      height: 40px;
                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                }
              }
            }
            &__thirdStep{
              display: flex;
              background-color: #ededed;
              border: 1px solid #d0d0d0;
              .hologram{
                padding: 10px;
                width: 50%;
                ul{
