                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                  .selectFile{
                    padding: 7.5px 0 15px;
                    display: flex;
                    input{
                      width: 0.01px;
                      height: 0.01px;
                      opacity: 0;
                      overflow: hidden;
                      z-index: -1;
                    }
                    input + label{
                      margin: 0 65px 0 0;
                      min-width: 160px;
                      display: block;
                      font: 1.063em/40px "Arial";
                      color: #fff;
                      text-align: center;
                      text-decoration: none;
                      background-color: #427b94;
                    }
                    input:focus + label,
                    input + label:hover{
                      background-color: #009fff;
                    }
                    input + label{
                      cursor: pointer;
                    }
                    input:focus + label{
                      outline: 1px solid #fff;
                      outline: -webkit-focus-ring-color auto 5px;
                    }
                    input + label * {
                      pointer-events: none;
                    }
                    .i-arrows,
                    .i-arrows-alt,
                    .i-refresh{
                      width: 40px;
                      height: 40px;
                      margin: 0 5px;
                      text-align: center;
                      background-color: #427b94;
                      border: 1px solid #d0d0d0;
                      i{
                        font-size: 2em;
                        line-height: 40px;
                        color: #fff;
                        &:hover{
                          cursor: pointer;
                        }
                      }
                    }
                  }
                }
              }
            }
            .backOrForward{
              display: flex;
              justify-content: space-around;
              a{
                margin: 45px 0 35px;
                padding: 0 35px;
                min-width: 215px;
                display: block;
                font: 1.25em/40px "Arial";
                color: #fff;
                text-align: center;
                text-transform: uppercase;
                text-decoration: none;
                background-color: #009fff;
              }
            }
          }
        }
      }
    }
  }
}